import React from 'react';
import styles from './Projects.module.css';

function Projects() {
  return (
    <div id='work' className={styles.background}>
        <div className={styles.project}>
            <h1>Projects</h1>
            <p>Take a look at some of my projects!</p>
        </div>

        <div className={styles.projectBox}>
            <div className={styles.leftBox}>
                <div className={styles.titleBox}>
                    <h2 className={styles.outline}>Clothing_store</h2>
                </div>
                <div className={styles.descBox}>
                    <p>Built With: Python | Django | javaScript | Stripe Payment | Bcrypt Hash</p>
                    <p>This web app is my first e-commerce store. The clothing store uses Stripe Payment and has an easy to use checkout system. Logged in users will have their orders saved to their account. </p>
                </div>
                <div className={styles.buttonBox}>
                    <div className={styles.box}>
                        <a href="https://github.com/jerryochoa8a/clothing_store">
                            <div className={styles.gitImage}/>
                        </a>
                    </div>
                    <div className={styles.box}>
                    <a href="http://ec2-3-101-47-30.us-west-1.compute.amazonaws.com/">
                            <div className={styles.wwwImage}/>
                        </a>
                    </div>
                    <div className={styles.box}></div>
                </div>
            </div>
            <div className={styles.rightBox1}></div>
        </div>
        <div className={styles.line}/>


        <div className={styles.projectBox}>
            <div className={styles.leftBox}>
                <div className={styles.titleBox}>
                    <h2 className={styles.outline}>Magic 8ball</h2>
                </div>
                <div className={styles.descBox}>
                    <p>Built With: React | JSX | javaScript | Node.js, Express.js | Yelp API</p>
                    <p>Designed to help the user make a decision on where to eat. This decision is made by random and picks from what’s around the given zip code.</p>
                </div>
                <div className={styles.buttonBox}>
                    <div className={styles.box}>
                        <a href="https://github.com/jerryochoa8a/magic-8ball/tree/master">
                            <div className={styles.gitImage}/>
                        </a>
                    </div>
                    {/* <div className={styles.box}>
                    <a href="">
                            <div className={styles.wwwImage}/>
                        </a>
                    </div> */}
                    <div className={styles.box}></div>
                </div>
            </div>
            <div className={styles.rightBox2}></div>
        </div>
        <div className={styles.line}/>


        <div className={styles.projectBox}>
            <div className={styles.leftBox}>
                <div className={styles.titleBox}>
                    <h2 className={styles.outline}>Appt. App - Front End</h2>
                </div>
                <div className={styles.descBox}>
                    <p>Built With: React Native |  javaScript </p>
                    <p>Practice with react native and javaScript. The drawer module was used for routing through the apps pages. Complete clickable prototype.</p>
                </div>
                <div className={styles.buttonBox}>
                    <div className={styles.box}>
                        <a href="https://github.com/jerryochoa8a/appt.app.frontend">
                            <div className={styles.gitImage}/>
                        </a>
                    </div>
                    {/* <div className={styles.box}>
                    <a href="">
                            <div className={styles.wwwImage}/>
                        </a>
                    </div> */}
                    <div className={styles.box}></div>
                </div>
            </div>
            <div className={styles.rightBox3}>
                {/* <div className={styles.iphone}/> */}
            </div>
        </div>
        <div className={styles.line}/>

        
    </div>
  );
}

export default Projects;